.movies .movie{
    min-width:500px;
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight:300;
    padding:20px;
    border-radius: 5px;
    color: #b2b3bc;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie img{
    position:relative;
    top:-50px;
    max-width: 150px;
    margin-right:30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);

}

.movie .movie__title,
.movie .movie__year {
    margin:0;
    font-weight:500;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size:24px;
    color: #2c2c2c;
}

.movie .movie__genre {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 2px;
}

.genres li,
.movie .movie__year {
    margin-right:10px;
    font-size:14px;
}